.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.modalPosition {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.modal {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  z-index: 1;
}

img {
  max-width: 800px;
  max-height: 600px;
}
